body {
  background-color: black;
  color: white;
}

#movies {
  text-align: center;
}

#movies .box {
  width: 200px;
  margin: 20px;
  cursor: pointer;
  display: inline-block;
  position: relative;
}

#movies .box .center {
  text-align: center;
}

#movies .box img {
  width: 200px;
  height: 295px;
  background-image: url(data:image/jpeg;base64,);
  transition: all 0.4s;
  background-size: contain;
  position: relative;
  z-index: 10;
}

#movies .box .type {
  text-transform: capitalize;
  color: gray;
}

#movies .box .title {
  font-size: 20px;
}

#movies .box .title.main {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

#movies .box .details {
  pointer-events: none;
  opacity: 0;
  z-index: 10;
  top: 0;
  transition: all 0.4s;
  position: absolute;
  height: 100%;
  width: 100%;
  text-align: left;
}

#movies .box .details .list {
  margin: 5px 0;
}

#movies .box .details .rating {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 5px;
}

#movies .box .details .rating li {
  border-top: solid 1px gray;
  padding: 5px 0;
}

#movies .box:hover {
  z-index: 100;
}

#movies .box:hover img {
  filter: blur(30px);
}

#movies .box:hover .details.show {
  opacity: 1;
}

#search {
  text-align: center;
}

#search input {
  width: 30%;
  transition: width 0.4s ease-in-out;
  margin: 32px;
  background: url(search.0f7ddc24583501b1de6e.png) no-repeat;
  background-size: 32px;
  padding: 8px;
  padding-left: 32px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #F7F7F7;
  /* When the input field gets focus, change its width to 100% */
}

#search input:focus {
  width: 60%;
  outline: none;
  background-color: #eaeaea;
}

#loader {
  background: url("data:image/svg+xml,%3Csvg width='30px' height='30px' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100' preserveAspectRatio='xMidYMid' class='lds-dual-ring' style='background: none;'%3E%3Ccircle cx='50' cy='50' ng-attr-r='%7B%7Bconfig.radius%7D%7D' ng-attr-stroke-width='%7B%7Bconfig.width%7D%7D' ng-attr-stroke='%7B%7Bconfig.stroke%7D%7D' ng-attr-stroke-dasharray='%7B%7Bconfig.dasharray%7D%7D' fill='none' stroke-linecap='round' r='40' stroke-width='4' stroke='%23fdfdfd' stroke-dasharray='62.83185307179586 62.83185307179586' transform='rotate(191.92 50 50)'%3E%3CanimateTransform attributeName='transform' type='rotate' calcMode='linear' values='0 50 50;360 50 50' keyTimes='0;1' dur='1s' begin='0s' repeatCount='indefinite'%3E%3C/animateTransform%3E%3C/circle%3E%3C/svg%3E");
  width: 30px;
  height: 30px;
  margin: 0 auto;
}

#loader.detail {
  position: absolute;
  top: 42%;
  left: 42%;
  z-index: 100;
}

#not-found {
  text-align: center;
}

@media screen and (min-width: 769px) {
  body {
    padding: 20px 200px;
  }
  #movies .box:hover img {
    transform: scale(1.2);
    filter: none;
    z-index: 100;
  }
  #movies .box:hover .details {
    background-color: black;
  }
  #movies .box:hover .details.show {
    margin-left: 220px;
    padding: 0 15px;
  }
}
